html,
body {
	height: 100%;
}

a {
	color: #007aff;
}
.search-bar {
	width: 20rem;
}

@media screen and (max-width: 40em) {
	.search-bar {
		width: 100%;
	}
}

.node-image {
	height: 3rem;
	width: 3rem;
}

.mh-100 {
	max-height: 100%;
}

.mv05 {
	margin-top: 0.125rem;
	margin-bottom: 0.125rem;
}

.min-h2 {
	min-height: 2rem;
}

.min-w2 {
	min-width: 2rem;
}

.max-h-5-rows {
	max-height: 246px;
}

.pointer-events-none {
	pointer-events: none;
}

.pointer-events-all {
	pointer-events: all;
}

.zoom-in {
	cursor: zoom-in;
}

.on:focus {
	outline: none;
}

.blue {
	color: #007aff !important;
}

.bg-map-beige {
	background-color: rgb(229, 227, 223);
}
